.search-filtering {
    background-color: #282c34;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-bottom: 20px;
}

.search-list {
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0px 50px 0px;
}

@media only screen and (max-width: 750px) {

    .search-filtering {
        flex-direction: column;
        
    }

}